.button {
	font: inherit;
	border: 1px solid #4f005f;
	border-radius: 5px;
	background: #4f005f;
	color: white;
	padding: 0.25rem 1rem;
	margin-bottom: 0.5rem;
	cursor: pointer;
}

.button:hover,
.button:active {
	background: #741188;
	border-color: #741188;
}

.button:focus {
	outline: none;
}
